.hero{
    height: 500px;
    width: 100%;
    /* background-color: grey; */
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    
}
.left-section{
    width: 100%;
    display: grid;
    place-content: center;
}
.hero-image{
    margin-top: 30px;
    width: 500px;
    height: 380px;
    padding-top: 20px;
}
.right-section{
    width: 100%;
    display: grid;
    place-content: center;
}
